.carousel {
  overflow-x: scroll;
  white-space: nowrap;
  height: 640px;
  &::-webkit-scrollbar {
    display: none;
  }
}

.carouselSlides {
  display: inline-flex;
  transition: transform 0.3s ease-in-out;
  height: 100%;
  position: relative;

  & .carouselSlide {
    position: absolute;
    width: 100vw;
    height: 100%;
    transition: transform 0.5s ease-in-out;
    display: none;

    & img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    &.current {
      z-index: 10;
      display: block;
    }

    &.previous {
      transform: translateX(-100%);
      display: block;
    }

    &.next {
      transform: translateX(100%);
      display: block;
    }
  }
}

.carouselContainer{
  position: relative;
  &:hover{
    .navButton{
      display: flex;
    }
  }
  .navButton{
    position: absolute;
    top: calc(50% - 50px);
    z-index: 20;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    display: none;
    align-items: center;
    justify-content: center;
    transition: transform 0.3s ease-in-out;
    cursor: pointer;
    
    span{
      font-size: 35px;
      color: #ffffff;
      opacity: 0.5;
      line-height: 0px;
    }

    &:hover{
      background-color: #7a769d8c;
      span{
        opacity: 1;
      }
    }
    &.btnPrev {
      left: 0%;
    }
    &.btnNext{
      right: 0%;
    }
  }

}