.seccion2 {
    background: linear-gradient(180deg, rgba(11, 108, 184, 0.3) 20%, rgba(11, 108, 184, 0.1) 60.5%, rgba(11, 108, 184, 0.3) 100%);
    position: relative;

    .backImage {
        overflow: hidden;
        width: 100%;

        img {
            width: 120%;
            height: 620px;
        }
    }

    .cardContainer {
        position: absolute;
        z-index: 10;
        width: 100%;
        display: flex;
        height: 100%;
        align-items: center;
        justify-content: center;

        h2{
            margin: 0px;
        }
        .cardContent {
            background-color: #ffffff;
            max-width: 820px;
            width: 90%;
            height: 420px;
            border-radius: 20px;
            padding: 40px;
            
            li {
                line-height: 60px;
                font-size: 1.25rem;
                color: #015AA1;
            }
        }
    }
}