.seccion1 {
    background: linear-gradient(180deg, rgb(255 255 255 / 51%) 20%, rgba(11, 108, 184, 0.1) 60.5%, rgba(11, 108, 184, 0.3) 100%);
    display: flex;
    flex-direction: row;
    padding: 60px 100px;
    height: 670px;

    .content {
        display: flex;
        flex-direction: column;
        margin-right: 120px;

        p {
            font-family: 'Ruda', sans-serif;
            font-style: normal;
            font-weight: 400;
            font-size: 24px;
            line-height: 48px;
        }
    }
    .profile{
        margin-right: 8%;
        position: relative;
        display: flex;
        justify-content: center;


        .profileImage{
            position: relative;
            z-index: 2;
            width: 380px;
            display: flex;
            align-items: center;

            img{
                width: 100%;
                height: fit-content;
                border-radius: 12px;
                box-shadow: 6px 4px 10px rgba(0, 0, 0, 0.25);
            }
        }
        .pointsStart{
            position: absolute;
            left: 0;
            margin-left: -56px;
            top: 45px;
        }
        .pointsEnd{
            position: absolute;
            right: 0;
            bottom: 45px;
            margin-right: -56px;
            z-index: 0;
        }
    }
}
@media only screen and (max-width: 600px) {
    .seccion1 {
        flex-direction: column;
        padding: 40px 40px;
        .content {
            margin-right: 0px;
            margin-bottom: 10px;
            text-align: justify;
            h2 {
                font-size: 36px;
                line-height: 20px;
                color: #002E52;
            }
    
            p {
                font-family: 'Ruda', sans-serif;
                font-style: normal;
                font-weight: 400;
                font-size: 16px;
                line-height: 40px;
            }
        }
    }
    .profileImage{
        max-width: 300px;
        
    }
}