.header {
    height: 60px;
    padding: 5px 30px;
    width: 100%;
    position: fixed;
    top: 0;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
    background-color: white;
    transition: all 0.3s ease-in-out;
    z-index: 100;

    &.solid{
        background: none;
        box-shadow: none;
        span{
            color: #ffffff;
        }
        .menu li a {
            color: #ffffff;
        }
    }
    .menu {
        display: inline-flex;

        li {
            height: 60px;
            display: flex;
            align-items: center;
            margin: 0px 10px;
            list-style-type: none;

            a {
                text-decoration: none;
                font-style: normal;
                font-weight: 400;
                font-size: 16px;
                line-height: 74px;
                color: #0578DD;
            }
        }

    }
}